<script>
export default {
    name: 'Content',
    methods: {
        handleScroll() {
            var winHeight = window.innerHeight;
            // var docHeight = document.documentElement.scrollHeight;
            var section1pos = this.$refs.section1.getBoundingClientRect();
            var section2pos = this.$refs.section2.getBoundingClientRect();
