            var section4pos = this.$refs.section4.getBoundingClientRect();
            // var scrollPos = window.scrollY;
            var imageMargin = winHeight * .04;
            // var halfImagePos = winHeight * .92;

            // Nav Text Changer
            if (section1pos.top > imageMargin) {
                this.$emit('changeNavText', '');
            }
            if (section1pos.top <= imageMargin && section2pos.top > imageMargin) {
                this.$emit('changeNavText', 'The Layers');
            }

            if (section2pos.top <= imageMargin && section3pos.top > imageMargin) {
                this.$emit('changeNavText', 'The Magnetic Field');
            }

            if (section3pos.top <= imageMargin && section4pos.top > imageMargin) {
                this.$emit('changeNavText', 'The Tectonic Plates');
            }

            if (section4pos.top <= imageMargin) {
                this.$emit('changeNavText', 'The Earth is a Sphere');
            }

                // Layer 1
            var layer1textpos = this.$refs.layer1text.getBoundingClientRect().top;
            var halflayer1post = (this.$refs.layer1text.getBoundingClientRect().height / 2) + 200;
            var change1Here = halflayer1post - winHeight/2 + 300;
            if (change1Here >= layer1textpos) {
                this.$refs.layer1.style.opacity = "1";
                this.$refs.layerText.style.opacity = "1";
                this.$refs.layerText.innerHTML = "21.7 - 43.5 miles"
            } else {
                this.$refs.layer1.style.opacity = "0";
                this.$refs.layerText.style.opacity = "0";
            }
                // Layer 2
            var layer2textpos = this.$refs.layer2text.getBoundingClientRect().top;
            var halflayer2post = (this.$refs.layer2text.getBoundingClientRect().height / 2) + 200;
            var change2Here = halflayer2post - winHeight/2 + 250;
            if (change2Here >= layer2textpos) {
                this.$refs.layer2.style.opacity = "1";
                this.$refs.layerText.innerHTML = "21.7 - 43.5 miles"
            } else {
                this.$refs.layer2.style.opacity = "0";
            }

                // Layer 3
            var layer3textpos = this.$refs.layer3text.getBoundingClientRect().top;
            var halflayer3post = (this.$refs.layer3text.getBoundingClientRect().height / 2) + 200;
            var change3Here = halflayer3post - winHeight/2 + 250;
            if (change3Here >= layer3textpos) {
                this.$refs.layer3.style.opacity = "1";
            } else {
                this.$refs.layer3.style.opacity = "0";
            }

                // Layer 4
            var layer4textpos = this.$refs.layer4text.getBoundingClientRect().top;
            var halflayer4post = (this.$refs.layer4text.getBoundingClientRect().height / 2) + 200;
            var change4Here = halflayer4post - winHeight/2 + 250;
            if (change4Here >= layer4textpos) {
                this.$refs.layer4.style.opacity = "1";
                this.$refs.layerText.style.color = "#F0F4F8";
                this.$refs.layerText.style.backgroundColor = "transparent";

            } else {
                this.$refs.layer4.style.opacity = "0";
                this.$refs.layerText.style.color = "#212121";
                this.$refs.layerText.style.backgroundColor = "#F0F4F8";
            }

            var textChange = this.$refs.layer4text.getBoundingClientRect().bottom - halflayer4post;
            if (textChange <= 200) {
                this.$refs.layerText.style.opacity = "0";
            }
            
        },
        wrapperHeight() {
            // Graphic Wrapper is length of content + half of image
                var section2pos = this.$refs.section2.getBoundingClientRect();
                var wrapperHeight = this.$refs.layerSectionWrapper.getBoundingClientRect().height;
                var idealWrapperHeight = wrapperHeight + section2pos.height/2 - 75;
                this.$refs.layersGraphicWrapper.style.height = idealWrapperHeight + "px";

            // Set height of line to continue until magnetic field
            // NEED TO DO
        }

    },
    created() {
        window.addEventListener('resize', this.wrapperHeight);
